import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { signup, signin } from '../../actions/auth';
import { ReactComponent as Logo } from './Logo.svg';
import "./Auth.scss";


const Auth = () => {
    const dispatch = useDispatch();
    const history = useHistory();

    const initialState = { firstName: '', lastName: '', email: '', password: '', confirmPassword: '', profile_picture: 'data:image/png;base64,' }
    const [formData, setFormData] = useState(initialState);
    const [isSignUp, setIsSignUp] = useState(false);

    const handleChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value })
    }

    const handleSubmit = (e) => {
        e.preventDefault()

        if (isSignUp) {
            dispatch(signup(formData, history))
        } else {
            dispatch(signin(formData, history))
        }
    }
    return (
        <div className="auth">
            <div className="background">
                <div className="background-overlay"></div>
            </div>
            <div className="form-section">
                {
                    isSignUp ? (
                        <div className="signup-form form">

                            <div className="logo">
                                <Logo className='icon logo-icon' />
                                <h1>Keeper</h1>
                            </div>

                            <div className="form-title">
                                <h1>Sign Up</h1>
                            </div>

                            <div className="form-body">
                                <form method="post">
                                    <div className="name-inputs">
                                        <div className="form-input nameInput">
                                            <h4>First Name</h4>
                                            <input type="text" name="firstName" required onChange={handleChange} />
                                        </div>
                                        <div className="form-input nameInput">
                                            <h4>Last Name</h4>
                                            <input type="text" name="lastName" required onChange={handleChange} />
                                        </div>
                                    </div>
                                    <div className="form-input email">
                                        <h4>Email</h4>
                                        <input type="email" name="email" required onChange={handleChange} />
                                    </div>
                                    <div className="form-input password">
                                        <h4>Password</h4>
                                        <input type="password" name="password" required onChange={handleChange} />
                                    </div>
                                    <div className="form-input confirm-password">
                                        <h4>Confirm Password</h4>
                                        <input type="password" name="confirmPassword" required onChange={handleChange} />
                                    </div>
                                    <div className="submit-btn">
                                        <button className="btn" type="submit" onClick={handleSubmit}>Submit</button>
                                    </div>
                                </form>
                                <div className="switch-form">
                                    <p>Already have an account ?</p>
                                    <button onClick={() => setIsSignUp(false)}>Sign in</button>
                                </div>
                            </div>
                        </div>
                    ) : (<div className="signin-form form">

                        <div className="logo">
                            <Logo className='icon logo-icon' />
                            <h1>Keeper</h1>
                        </div>
                        <div className="form-title">
                            <h1>Sign In</h1>
                        </div>
                        <div className="form-body">
                            <form action="" method="post">
                                <div className="form-input email">
                                    <h4>Email</h4>
                                    <input type="email" name="email" required onChange={handleChange} />
                                </div>
                                <div className="form-input password">
                                    <h4>Password</h4>
                                    <input type="password" name="password" required onChange={handleChange} />
                                </div>
                                <div className="submit-btn">
                                    <button className="btn" type="submit" onClick={handleSubmit}>Submit</button>
                                </div>
                            </form>
                            <div className="switch-form">
                                <p>Don't have an account ?</p>
                                <button onClick={() => setIsSignUp(true)}>Sign Up</button>
                            </div>
                        </div>
                    </div>)
                }
            </div>

            <div className="slogan-side">
                <div className="slogan-typo">
                    <h4><span>Keep your </span>Notes, Reminders and Tasks<span> in one place</span></h4>
                </div>
            </div>
        </div>
    )
}

export default Auth
